import React, {useState} from 'react'
import "./App.css";
import Header from './components/Header'
import Form from './components/Form'
import ShopList from './components/ShopList'


function App() {
  const [input, setInput] = useState('')
  const [shopList,setShopList] = useState([])
  return (
    <div className="App">
      <Header />
      <Form setShopList={setShopList} setInput={setInput} input={input} shopList={shopList}/>
      <ShopList input={input} shopList={shopList} setShopList={setShopList} />
    </div>
  );
}

export default App;
