import React from "react";

function List( { text, shopList, setShopList, list } ) {
  const deleteHandler = () =>{
     setShopList(shopList.filter((item) => item.id !== list.id ))
  }
  const deleteDone = () =>{
     setShopList(shopList.map((done) => {
         if(done.id === list.id){
             return{...done, completed:!done.completed}
         }
         return done;
     }))
  }

  return (
    <ul>
      <li className={`${list.completed ? "completed" : ""}`}>{text}</li>
      <button onClick ={deleteDone}className="btnCheck">
        <i className="fas fa-check"></i>
      </button>
      <button onClick={deleteHandler} className="btnTrash">
        <i className="fas fa-trash"></i>
      </button>
    </ul>
  );
}

export default List;
