import React from "react";
import List from './List'

function ShopList( {shopList, setShopList} ) {
  return (
    <div className="shop-list">
       {shopList.map((list)=>(
           <List list={list} text={list.text} key ={list.id} shopList={shopList} setShopList={setShopList}/>
        ))}
          
    </div>
    )
}

export default ShopList;
