import React from "react";
import { v4 as uuidv4 } from "uuid";
function Form({ setShopList, setInput, input, shopList }) {
  const textHandler = (e) => {
    setInput(e.target.value);
  };
  const submitHandler = (e) => {
    e.preventDefault();

    setShopList([...shopList, { text: input, id: uuidv4(), completed:false }]);

    setInput("");
  };

  return (
    <form onSubmit={submitHandler}>
      <input value={input} onChange={textHandler} type="text" />
      <button className="plus">
        <i className="fas fa-plus-square"></i>
      </button>
    </form>
  );
}

export default Form;
